export default {
	data() {
		return {
			title: "启奥软件",
			language: "简体",
			languageList: ['简体','英文'],
			selectLang: false,
			login: true,
			telLogin: false,
			time: 60,
			sendMsg: '发送验证码',
			sendNum: 0,
			loginForm: {
				username: "",
				password: "",
				savePassword: false,
				tel: "",
				check: ""
			},
			disabled: false
		}
	},
	methods: {
		selectLanguage() {
			this.selectLang = !this.selectLang;
		},
		closeselect() {
			this.selectLang = false;
		},
		selectedL(lang) {
			this.language = lang;
			this.selectLang = false;
		},
		telLoginP() {
			this.login = !this.login;
			this.telLogin = !this.telLogin;
		},
		getCheck() {
			var time = parseInt(this.time);
			if (time <= 0) {
				this.sendMsg = '发送验证码';
				this.sendNum = 0;
				this.time = 60;
				return false;
			}
			this.time = parseInt(this.time) - 1;
			this.sendMsg = this.time;
			this.sendNum = 1;
			setTimeout(this.getCheck, 1000);
		},
		userLogin() {
			if (this.login) {
				if (!this.loginForm.username) {
					uni.showToast({
						title: '请输入用户名',
						duration: 2000,
						icon: 'none'
					});
					return;
				}
				if (!this.loginForm.password) {
					uni.showToast({
						title: '请输入密码',
						duration: 2000,
						icon: 'none'
					});
					return;
				}	
			} else {
				if (!this.loginForm.tel) {
					uni.showToast({
						title: '请输入电话号码',
						duration: 2000,
						icon: 'none'
					});
					return;
				}
				if (!this.loginForm.check) {
					uni.showToast({
						title: '请输入验证码',
						duration: 2000,
						icon: 'none'
					});
					return;
				}
			}
			if (this.loginForm.username !== 'admin' || this.loginForm.password !== 'Aa000000') {
				uni.showToast({
					title: '密码不正确',
					duration: 2000,
					icon: 'none'
				});
				return;
			}
			uni.switchTab({
			    url: '/pages/index/index'
			});
			console.log(this.loginForm);
		},
		toSavePassword() {
			this.loginForm.savePassword = !this.loginForm.savePassword;
		}
	}
}
